'use client';

import { useState } from 'react';
import UploadPage from '@/components/upload-page';
import LoadingPage from '@/components/loading-page';
import ResultPage from '@/components/result-page';
import { Property } from '@/types/property';

type AppStep = 'upload' | 'loading' | 'result';

// 生成模擬財產資料
function generateMockProperties(): Property[] {
  const properties: Property[] = [];
  const cities = ['台北市', '新北市', '桃園市', '台中市', '台南市', '高雄市'];
  
  // 舊制地目種類（建築用地、直接生產用地、交通水利用地、其他用地）
  const oldLandTypes = {
    // 建築用地
    building: ['建', '雜', '祠', '鐵', '公', '墓'],
    // 直接生產用地
    production: ['田', '旱', '林', '養', '牧', '礦', '鹽', '池'],
    // 交通水利用地
    infrastructure: ['道', '線', '水', '溜', '溝'],
    // 其他用地
    other: ['提', '原'],
  };
  // 合併所有地目
  const allLandTypes = [
    ...oldLandTypes.building,
    ...oldLandTypes.production,
    ...oldLandTypes.infrastructure,
    ...oldLandTypes.other,
  ];
  
  const locations = ['中正路', '中山路', '信義路', '仁愛路', '和平路', '忠孝路', '復興路', '民生路'];
  
  // 生成土地（地價稅）
  const landCount = Math.floor(Math.random() * 5) + 1;
  for (let i = 0; i < landCount; i++) {
    properties.push({
      propertyType: '土地',
      ownershipRatio: `${Math.floor(Math.random() * 100) + 1}%`,
      area: `${(Math.random() * 500 + 50).toFixed(2)}`,
      currentValue: `NT$ ${(Math.random() * 10000000 + 1000000).toLocaleString()}`,
      period: `${2020 + Math.floor(Math.random() * 4)}年${Math.floor(Math.random() * 12) + 1}月`,
      owner: '張三',
      landType: allLandTypes[Math.floor(Math.random() * allLandTypes.length)],
      city: cities[Math.floor(Math.random() * cities.length)],
      location: `${locations[Math.floor(Math.random() * locations.length)]}${Math.floor(Math.random() * 100) + 1}號`,
      numerator: `${Math.floor(Math.random() * 3) + 1}`,
      denominator: `${Math.floor(Math.random() * 3) + 1}`,
      trustNote: Math.random() > 0.7 ? '信託' : '',
      registrationTime: `${2020 + Math.floor(Math.random() * 4)}/${String(Math.floor(Math.random() * 12) + 1).padStart(2, '0')}/${String(Math.floor(Math.random() * 28) + 1).padStart(2, '0')}`,
    });
  }
  
  // 生成房屋（房屋稅）
  const houseCount = Math.floor(Math.random() * 5) + 1;
  for (let i = 0; i < houseCount; i++) {
    properties.push({
      propertyType: '房屋',
      ownershipRatio: `${Math.floor(Math.random() * 100) + 1}%`,
      area: `${(Math.random() * 200 + 30).toFixed(2)}`,
      currentValue: `NT$ ${(Math.random() * 15000000 + 2000000).toLocaleString()}`,
      period: `${2020 + Math.floor(Math.random() * 4)}年${Math.floor(Math.random() * 12) + 1}月`,
      owner: '張三',
      landType: allLandTypes[Math.floor(Math.random() * allLandTypes.length)],
      city: cities[Math.floor(Math.random() * cities.length)],
      location: `${locations[Math.floor(Math.random() * locations.length)]}${Math.floor(Math.random() * 100) + 1}號`,
      numerator: `${Math.floor(Math.random() * 3) + 1}`,
      denominator: `${Math.floor(Math.random() * 3) + 1}`,
      trustNote: Math.random() > 0.7 ? '信託' : '',
      registrationTime: `${2020 + Math.floor(Math.random() * 4)}/${String(Math.floor(Math.random() * 12) + 1).padStart(2, '0')}/${String(Math.floor(Math.random() * 28) + 1).padStart(2, '0')}`,
    });
  }
  
  // 生成車輛（牌照稅）
  const vehicleCount = Math.floor(Math.random() * 5) + 1;
  for (let i = 0; i < vehicleCount; i++) {
    const carYear = 2020 + Math.floor(Math.random() * 4);
    properties.push({
      propertyType: '車輛',
      ownershipRatio: `${Math.floor(Math.random() * 3000) + 1000}cc`, // 汽缸容量
      area: '-',
      currentValue: `NT$ ${(Math.random() * 2000000 + 500000).toLocaleString()}`,
      period: `${carYear}年${Math.floor(Math.random() * 12) + 1}月`,
      owner: '張三',
      landType: `${carYear}年`, // 車年
      city: cities[Math.floor(Math.random() * cities.length)],
      location: `BAN-${String(Math.floor(Math.random() * 9999) + 1).padStart(4, '0')}`,
      // 車輛通常沒有持份問題，所以不填寫持分分子和持分分母
      numerator: undefined,
      denominator: undefined,
      trustNote: Math.random() > 0.8 ? '信託' : '',
      registrationTime: `${carYear}/${String(Math.floor(Math.random() * 12) + 1).padStart(2, '0')}/${String(Math.floor(Math.random() * 28) + 1).padStart(2, '0')}`,
    });
  }
  
  return properties;
}

export default function Home() {
  const [step, setStep] = useState<AppStep>('upload');
  const [properties, setProperties] = useState<Property[]>([]);

  const handleFileSelect = () => {
    setStep('loading');
    
    // 5秒後進入結果頁面，並生成模擬數據
    setTimeout(() => {
      const mockProperties = generateMockProperties();
      setProperties(mockProperties);
      setStep('result');
    }, 5000);
  };

  return (
    <main className="min-h-screen bg-background">
      {step === 'upload' && <UploadPage onFileSelect={handleFileSelect} />}
      {step === 'loading' && <LoadingPage />}
      {step === 'result' && (
        <ResultPage
          properties={properties}
          onReset={() => {
            setStep('upload');
            setProperties([]);
          }}
        />
      )}
    </main>
  );
}
